(function () {
    'use strict';

    angular
        .module('musixiseApp')
        .config(pagerConfig);

    function pagerConfig(uibPagerConfig, paginationConstants) {
        uibPagerConfig.itemsPerPage = paginationConstants.itemsPerPage;
        uibPagerConfig.previousText = '';
        uibPagerConfig.nextText = '';
    }
})();
